@font-face {
  font-family: 'Space Ranger Title';
  src: url(/tp/static/media/SpaceRangerTitle.8b44bfa5.woff2) format('woff2'),
  url(/tp/static/media/SpaceRangerTitle.a0e093f7.woff) format('woff');
  font-weight: normal;
  font-style: normal;
}

body {
  margin : 0px;
  color: #fff;
  background-color: #000;
  font-family: 'Trebuchet MS', -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
  "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
  sans-serif;
  font-weight: normal;
  font-style: normal;

  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

html,body {
    margin: 0;
    padding: 0;
    width: 100%;
    text-align: center;
}

html, body {
  overflow-x: hidden;
}

body {
  position: relative;
  width: 100vw;
  height: 100vh;
}

h1 {
    font-family: 'Space Ranger Title', monospace;
    font-weight: normal;
    font-style: normal;
}

em {
  text-decoration: underline;
}


.locked-overlay-top {
  position: fixed;
  top: 2em;
  width: 80%;
  left: 10%;
  height: 50px;
}


.locked-overlay-bottom {
  position: fixed;
  bottom: 2em;
  width: 80%;
  left: 10%;
}

.hidden-canvas {
  display: none;
}


.spacer {
  height: 150px;
}

button {
  box-shadow: 0 0 10px #00dcdc;
  background: #00bebe;
  color: #212121;
  font-family: 'Space Ranger Title';
  font-size: 1.2em;

  width: 96%;
  height: 3em;

  margin-bottom: 1em;
}

button.large-button {
  color: #00bebe;
  background: #212121;
  height: 6em;
}

button.not-important {
  color: #00bebe;
  background: #212121;
  height: 2.5em;
}

.depth-level-1,
.depth-level-2,
.depth-level-3,
.depth-level-4,
.depth-level-5,
.depth-level-6,
.depth-level-7,
.depth-level-8,
.depth-level-9,
.depth-level-10,
.depth-level-excavated {
    float: left;
    width: 29%;
    margin-left: 2%;
    margin-right: 2%;
    padding: 0;
    text-align: center;
    height: 80px;
    line-height: 80px;
    background-color: #565656;
    color: white;
    font-size: 12px;
    text-decoration: none;
    box-shadow: 0 0 10px #333333;
}
