.depth-level-2  { background-color: #606060; }
.depth-level-3  { background-color: #565656; }
.depth-level-4  { background-color: #505050; }
.depth-level-5  { background-color: #464646; }
.depth-level-6  { background-color: #404040; }
.depth-level-7  { background-color: #363636; }
.depth-level-8  { background-color: #303030; }
.depth-level-9  { background-color: #262626; }
.depth-level-10 { background-color: #202020; }*/
.depth-level-excavated {
    background-color: #222;
    border-color: #111;
    color: #999; font-style: italic; line-height: 0.2em;}


.exploration-result {
    width: 80vw;
    margin: 10vh auto auto;
}
.data-log {
    padding: 1em;
    width: 80vw;
    margin: auto;
    background-color: #565656;
    margin-bottom: 2rem;
}

.data-log .data-log-number {
    font-family: 'Space Ranger Title', monospace;
    font-weight: normal;
    font-style: normal;
    font-size: 10vh;
}
.genesis-button {
    float: left;
    width: 29%;
    margin-left: 2%;
    margin-right: 2%;
    padding: 0;
    text-align: center;
    height: 80px;
    line-height: 80px;
    background-color: #565656;
    color: white;
    font-size: 12px;
    text-decoration: none;
    box-shadow: 0 0 10px #333333;
}
.App {
    text-align: center;
}

.App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
    height: 40vmin;
}

.App-header {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
}

.App-link {
    color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

@keyframes App-logo-spin {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

.menu-button img {
    z-index: 200;
    width: 100%;
    height: 100%;
    font-size: 2em;

}

button.menu-button {
    z-index: 200;
    box-shadow: none;
    position: absolute;
    width: 2em;
    height: 2em;
    top: 0.59em;
    right: 0.67em;
    padding: 0;
    margin: 0;

    background-color: transparent;
    border: none;

    font-size: 1em;
}
.detailed-planet-image {
    width: 200px;
    height: 200px;
}

.planet-details {
    position: relative;
    width: 90%;
    height: 100%;
    margin: auto;
}

.scanning-planet-image {
    width: 2em;
    height: 2em;
}

.scanner-overlay, .scanner-background, .scanner-static-background, .scanner-info {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

.scanner-background, .scanner-static-background {
    z-index: 1;
}

.scanner-info {
    text-align: center;
    color: white;
    z-index: 10;
    padding-left: 10vw;
    padding-right: 10vw;
    width: 80vw;
}

.scanner-overlay, .scanner-overlay-locked {
    z-index: 100;
}

.scanner-static-background {
    opacity: 1.0;
    background-image: url("/images/scanner-interlace-01.png");
    background-size: 100% 100%;
}

.scanner-mode-locking > .scanner-background {
    background-color: black;/*#2471ff*/
}

.scanner-mode-locked > .scanner-background {
    opacity: 0.8;
    background-color: black;/*#007980;*/
}

.scanner-mode-explored > .scanner-background {
    opacity: 0.8;
    background-color: darkred;
}


.planet-link {
    float:left;
    display:block;
    padding: 0px;
    margin: 5px;
    width: 100px;
    height: 100px;
    border: 0px;
}

.planet-link img {
    width: 100px;
}
